        methods: {
            ...mapMutations(['setUserInfo']),
            getCollarCardNum() {
                request.http({
                    url: "/app/index/getCollarCardNum",
                    header: 'application/x-www-form-urlencoded',
                    data: {},
                    success: (res) => {
                        if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                            this.howMany = 0;
                            this.howMany = res.data.data + 1;
                        }
